.btn-collapse{
    width: 100%;
    height: 40px;
    color: white;
    background-color: #FF6060 ;
    border-radius: 10px;
    border-color: #FF6060;
    border-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 24px;
}
.collapse-container{
    width: 75%;
    height: 50%;
    display: flex;
    flex-direction: column;
    margin-left: 100px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.collapse-content{
    width: 100%;
    margin:0;
    background-color: #f7f7f7;
    border-radius: 10px
}
.collapse-content-text {
    width: 90%;
    height: 100%;
    margin-left: 20px;
    color: #FF6060;
    font-size: 22px;
}
.collapse-content-text-appartement {
    width: 90%;
    height: 100%;
    margin-left: 20px;
    color: #FF6060;
    font-size: 18px;
}
.arrow{

    transform: rotate(180deg);
    margin-right: 30px;

}

.arrow-up{
    margin-right: 30px;
}

@media only screen and (max-width: 1300px){
    
    .btn-collapse{
      
        font-size: 20px;
        margin-left: 0px;
    }
    .collapse-container{
        width: 100%;
        height: 50%;
        display: flex;
        flex-direction: column;
        margin-left: 20px;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .collapse-content-text {
        
        font-size: 16px;
    }
    
   
}
@media only screen and (max-width: 500px){
    
    .btn-collapse{
      
        font-size: 13px;
        width: 100%;
        border-radius: 5px;
    }
    .collapse-container{
        width: 100%;
        height: 50%;
        display: flex;
        flex-direction: column;
        margin-left: 0px;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .collapse-content-text {
        
        font-size: 12px;
    }
    
   
}